import axios from "axios"

const BASE_URL = "https://cee-player-service.curriki.org/api/v1"

const api = axios.create({
  baseURL: BASE_URL,
})

export const platformRegister = async (data) => {
  try {
    const token = localStorage.getItem("token")
    if (!token) {
      throw new Error("Auth token not found")
    }
    const response = await api.post("/platform/registerPlatform", data, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    return response.data
  } catch (error) {
    console.error("Error in platformRegister:", error)
    throw error
  }
}

