import React, { useEffect, useState } from "react"
import { useSearchParams } from "react-router-dom"
import { Link } from "react-router-dom"

import { Formik } from "formik"

import C2eHeadingSubText from "../../utils/C2eHeading"
import C2eLabel from "../../utils/C2eLabel"
import LoginInput from "../../utils/FormElements/loginInput"
import Button from "../../utils/Button"
import { useDispatch } from "react-redux"
import {
  CrossIcon,
  EyeIcon,
  HideEye,
  Loading,
} from "../../components/IconLibrary"

import {
  showErrorReducer,
  showSuccessReducer,
} from "../../Store/reducers/c2eSettings"
import C2eAlert from "../../utils/Alert"
import { verifyResetPasswordToken } from "../../Services/auth"

const NewResetPassword = ({
  showPassword,
  setshowPassword,
  handleUpdatePassword,
}) => {
  const dispatch = useDispatch()
  const [loading, setLoading] = useState("in-progress")
  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  let [searchParams] = useSearchParams()

  const verifyLink = async () => {
    try {
      await verifyResetPasswordToken(searchParams.get("token"))
      setLoading("success")
    } catch (e) {
      dispatch(showErrorReducer(e?.response?.data?.message))
      setLoading("failed")
    }
  }
  useEffect(() => {
    if (searchParams.get("token")) {
      verifyLink()
    } else {
      dispatch(showErrorReducer("Invalid token"))
    }
  }, [dispatch, searchParams])

  return (
    <div className="max-w-[448px] w-full flex gap-[70px] flex-col items-center justify-center">
      {/* heading */}
      <C2eHeadingSubText className="" heading="Reset Password" />
      <Formik
        initialValues={{
          newpassword: "",
          confirmPassword: "",
        }}
        validate={(values) => {
          const errors = {}
          const passwordRegex =
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&]).{8,26}$/
          if (!values.newpassword) {
            errors.newpassword = "Required"
          } else if (!passwordRegex.test(values.newpassword)) {
            errors.newpassword = `Password should be between 8 - 26 characters and must include at least one lowercase letter, one uppercase letter, one number, and one symbol`
          }
          if (!values.confirmPassword) {
            errors.confirmPassword = "Required"
          } else if (values.newpassword !== values.confirmPassword) {
            errors.confirmPassword = "Passwords do not match"
          }

          return errors
        }}
        onSubmit={async (values, { setSubmitting }) => {
          dispatch(showErrorReducer())
          const res = await handleUpdatePassword({
            token: searchParams.get("token"),
            password: values.newpassword,
          })
          if (res?.code === 200) {
            dispatch(showSuccessReducer("Password Updated!"))
            return res
          } else {
            dispatch(
              showErrorReducer(res?.data?.message || "Something went wrong!")
            )
          }
          setSubmitting(false)
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          setFieldValue,
          isSubmitting,
          /* and other goodies */
        }) => (
          <form onSubmit={handleSubmit} className="w-full">
            <C2eAlert type="error" />
            {loading !== "success" ? (
              <>
                <div className="w-full mb-10">
                  <div className="w-full flex flex-col items-start">
                    <div className="w-full">
                      <div className="w-full">
                        <C2eLabel text="New Password" />
                        <LoginInput
                          type={showNewPassword ? "text" : "password"}
                          placeholder="Enter new password"
                          name="newpassword"
                          crossIcon={
                            values.newpassword ? (
                              <CrossIcon w="10px" h="100%" />
                            ) : (
                              ""
                            )
                          }
                          inputIcon={
                            values.newpassword ? (
                              !showNewPassword ? (
                                <EyeIcon />
                              ) : (
                                <HideEye h="25px" />
                              )
                            ) : (
                              ""
                            )
                          }
                          onChange={handleChange}
                          onBlur={handleBlur}
                          setFieldValue={setFieldValue}
                          showPassword={showNewPassword}
                          setshowPassword={setShowNewPassword}
                          value={values.newpassword}
                        />
                        {errors.newpassword && touched.newpassword && (
                          <div className="error">{errors.newpassword}</div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>

                <div className="w-full mb-10">
                  <div className="w-full flex flex-col items-start">
                    <div className="w-full">
                      <div className="w-full">
                        <C2eLabel text="Confirm Password" />
                        <LoginInput
                          type={showConfirmPassword ? "text" : "password"}
                          placeholder="Confirm new password"
                          name="confirmPassword"
                          crossIcon={
                            values.confirmPassword ? (
                              <CrossIcon w="10px" h="100%" />
                            ) : (
                              ""
                            )
                          }
                          inputIcon={
                            values.confirmPassword ? (
                              !showConfirmPassword ? (
                                <EyeIcon />
                              ) : (
                                <HideEye h="25px" />
                              )
                            ) : (
                              ""
                            )
                          }
                          onChange={handleChange}
                          onBlur={handleBlur}
                          showPassword={showConfirmPassword}
                          setshowPassword={setShowConfirmPassword}
                          setFieldValue={setFieldValue}
                          value={values.confirmPassword}
                        />
                        {errors.confirmPassword && touched.confirmPassword && (
                          <div className="error">{errors.confirmPassword}</div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>

                <div className="flex items-center flex-col gap-2 justify-center">
                  <Button
                    type="submit"
                    bg="bg-[#084892]"
                    className="min-w-[242px] justify-center text-white shadow-btnShadow"
                    text={"Change Password"}
                    disabled={isSubmitting}
                    Icon={isSubmitting ? <Loading /> : ""}
                    cta={() => {}}
                  />
                  <div className="flex items-center">
                    <Link to="/login">
                      <Button
                        type="button"
                        bg="bg-transparent"
                        className="text-[#084892] p-0 !mb-0"
                        text={"Back to Login"}
                        cta={() => {}}
                      />
                    </Link>
                  </div>
                </div>
              </>
            ) : loading === "in-progress" ? (
              "loading"
            ) : (
              ""
            )}
          </form>
        )}
      </Formik>
    </div>
  )
}

export default NewResetPassword
