// auth.js
import axios from "axios"

const apiUrl = process.env.REACT_APP_BASE_URL;
const BASE_URL = "https://cee-player-service.curriki.org/api/v1/admin"

const api = axios.create({
  baseURL: BASE_URL,
})

export const signUp = async (email, password) => {
  try {
    const response = await api.post("/register", { email, password })
    return response.data
  } catch (error) {
    throw error
  }
}

export const signIn = async (email, password) => {
  const response = await api.post("/login", { email, password })
  return response.data
}

export const getProfileData = async () => {
  try {
    const token = localStorage.getItem("token")
    if (!token) {
      throw new Error("Auth token not found")
    }
    const response = await api.get("/getProfile", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    return response
  } catch (error) {
    throw error
  }
}

export const verifyResetPasswordToken = async (token) => {
  const response = await api.get(`/verifyResetPasswordToken?token=${token}`)
  return response.data
}

export const updatePassword = async (password, newPassword) => {
  try {
    const token = localStorage.getItem("token")
    if (!token) {
      throw new Error("Auth token not found")
    }
    const response = await api.patch(
      "/updatePassword",
      { password, newPassword },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    )
    return response
  } catch (error) {
    throw error
  }
}

export const forgetPassword = async (email) => {
  const response = await api.post("/forgetPassword", { email })
  return response.data
}

export const resetPassword = async (data) => {
  const response = await api.post("/resetPassword", data)
  return response.data
}
