import React, { useState } from "react"
import Button from "../../utils/Button"
import Settings from "../../assets/images/settings.svg"
import { Formik } from "formik"
import C2eAlert from "../../utils/Alert"
import C2eLabel from "../../utils/C2eLabel"
import LoginInput from "../../utils/FormElements/loginInput"
import { CrossIcon, EyeIcon, HideEye, Loading } from "../IconLibrary"
import { useDispatch } from "react-redux"
import {
  showErrorReducer,
  showSuccessReducer,
} from "../../Store/reducers/c2eSettings"
import { updatePassword } from "../../Services/auth"

const Setting = ({ userInitial }) => {
  const dispatch = useDispatch()
  const [showOldPassword, setShowOldPassword] = useState(false)
  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)

  return (
    <div className="w-full">
      <div className="flex flex-col justify-center gap-2 mb-[31px] items-start">
        <img className="w-[48px] h-[100%]" src={Settings} alt="logo" />
        <div className="flex flex-col gap-1">
          <h2 className="heading2">Settings</h2>
          <p className="text-[17px] leading-5 font-normal text-[#767676]">
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry.
          </p>
        </div>
      </div>
      <div className="flex flex-row gap-[86px]">
        <div className="w-[147px] h-[147px] rounded-[50%] bg-[#f0f0f0] flex justify-center items-center font-medium text-[#000]">
          {userInitial}
        </div>
        <div className="w-[1px] bg-[#D9D9D9]" />
        <div className="max-w-[430px] w-full flex flex-col items-center justify-center">
          <Formik
            initialValues={{
              name: "",
              username: "",
              oldpassword: "",
              newpassword: "",
              confirmPassword: "",
            }}
            validate={(values) => {
              const errors = {}
              const passwordRegex =
                /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&]).{8,26}$/
              if (!values.name) {
                errors.name = "Required"
              }
              if (!values.username) {
                errors.username = "Required"
              }
              if (!values.oldpassword) {
                errors.oldpassword = "Required"
              }
              if (!values.newpassword) {
                errors.newpassword = "Required"
              } else if (!passwordRegex.test(values.newpassword)) {
                errors.newpassword = `Password should be between 8 - 26 characters and must include at least one lowercase letter, one uppercase letter, one number, and one symbol`
              }
              if (!values.confirmPassword) {
                errors.confirmPassword = "Required"
              } else if (values.newpassword !== values.confirmPassword) {
                errors.confirmPassword = "Passwords do not match"
              }

              return errors
            }}
            onSubmit={async (values, { setSubmitting }) => {
              dispatch(showErrorReducer())

              const res = await updatePassword(
                values.oldpassword,
                values.newpassword
              )
              if (res?.code === 200) {
                dispatch(showSuccessReducer("Password Updated!"))
                return res
              } else {
                dispatch(
                  showErrorReducer(
                    res?.data?.message || "Something went wrong!"
                  )
                )
              }

              setSubmitting(false)
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
              isSubmitting,
              /* and other goodies */
            }) => (
              <form onSubmit={handleSubmit} className="w-full">
                <C2eAlert type="error" />
                <>
                  <div className="w-full mb-4">
                    <C2eLabel
                      text="Name:"
                      className="text-[#000] !font-medium"
                    />
                    <LoginInput
                      type="text"
                      placeholder="Enter your Name"
                      name="name"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      setFieldValue={setFieldValue}
                      value={values.name}
                    />
                    {errors.name && touched.name && (
                      <div className="error">{errors.name}</div>
                    )}
                  </div>

                  <div className="w-full">
                    <C2eLabel
                      text="Username:"
                      className="text-[#000] !font-medium"
                    />
                    <LoginInput
                      type="text"
                      placeholder="Enter your Username"
                      name="username"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      setFieldValue={setFieldValue}
                      value={values.name}
                    />
                    {errors.username && touched.username && (
                      <div className="error">{errors.username}</div>
                    )}
                  </div>

                  <div className="w-full my-6 border-b-[1px] border-b-[#084892]">
                    <p className="heading2 text-lg mb-2">Update Password</p>
                  </div>
                  <div className="w-full mb-4">
                    <div className="w-full flex flex-col items-center justify-center">
                      <div className="w-full">
                        <C2eLabel
                          text="Old Password:"
                          className="text-[#000] !font-medium"
                        />
                        <LoginInput
                          type={showOldPassword ? "text" : "password"}
                          placeholder="Enter old password"
                          name="oldpassword"
                          crossIcon={
                            values.oldpassword ? (
                              <CrossIcon w="10px" h="100%" />
                            ) : (
                              ""
                            )
                          }
                          inputIcon={
                            values.oldpassword ? (
                              !showOldPassword ? (
                                <EyeIcon />
                              ) : (
                                <HideEye h="25px" />
                              )
                            ) : (
                              ""
                            )
                          }
                          onChange={handleChange}
                          onBlur={handleBlur}
                          setFieldValue={setFieldValue}
                          showPassword={showOldPassword}
                          setshowPassword={setShowOldPassword}
                          value={values.oldpassword}
                        />
                        {errors.oldpassword && touched.oldpassword && (
                          <div className="error">{errors.oldpassword}</div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="w-full mb-4">
                    <div className="w-full flex flex-col items-center justify-center">
                      <div className="w-full">
                        <C2eLabel
                          text="New Password:"
                          className="text-[#000] !font-medium"
                        />
                        <LoginInput
                          type={showNewPassword ? "text" : "password"}
                          placeholder="Enter new password"
                          name="newpassword"
                          crossIcon={
                            values.newpassword ? (
                              <CrossIcon w="10px" h="100%" />
                            ) : (
                              ""
                            )
                          }
                          inputIcon={
                            values.newpassword ? (
                              !showNewPassword ? (
                                <EyeIcon />
                              ) : (
                                <HideEye h="25px" />
                              )
                            ) : (
                              ""
                            )
                          }
                          onChange={handleChange}
                          onBlur={handleBlur}
                          setFieldValue={setFieldValue}
                          showPassword={showNewPassword}
                          setshowPassword={setShowNewPassword}
                          value={values.newpassword}
                        />
                        {errors.newpassword && touched.newpassword && (
                          <div className="error">{errors.newpassword}</div>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="w-full mb-4">
                    <div className="w-full flex flex-col items-center justify-center">
                      <div className="w-full">
                        <C2eLabel
                          text="Confirm Password:"
                          className="text-[#000] !font-medium"
                        />
                        <LoginInput
                          type={showConfirmPassword ? "text" : "password"}
                          placeholder="Confirm new password"
                          name="confirmPassword"
                          crossIcon={
                            values.confirmPassword ? (
                              <CrossIcon w="10px" h="100%" />
                            ) : (
                              ""
                            )
                          }
                          inputIcon={
                            values.confirmPassword ? (
                              !showConfirmPassword ? (
                                <EyeIcon />
                              ) : (
                                <HideEye h="25px" />
                              )
                            ) : (
                              ""
                            )
                          }
                          onChange={handleChange}
                          onBlur={handleBlur}
                          showPassword={showConfirmPassword}
                          setshowPassword={setShowConfirmPassword}
                          setFieldValue={setFieldValue}
                          value={values.confirmPassword}
                        />
                        {errors.confirmPassword && touched.confirmPassword && (
                          <div className="error">{errors.confirmPassword}</div>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="flex items-end flex-col gap-2 justify-center">
                    <Button
                      type="submit"
                      bg="bg-[#084892]"
                      className="min-w-[143px] text-white shadow-btnShadow justify-center !rounded-[18px]"
                      text="Update Password"
                      disabled={isSubmitting}
                      Icon={isSubmitting ? <Loading /> : ""}
                      cta={() => {}}
                    />
                  </div>
                </>
              </form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  )
}

export default Setting
