import React, { useState } from "react"
import { DropdownIcon } from "../IconLibrary"
import { useNavigate } from "react-router"

const Header = (props) => {
  const { setTabs, userInitial, userName } = props
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [activeMenuItem, setActiveMenuItem] = useState(null)

  const navigate = useNavigate()
  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen)
  }

  const handleMenuItemClick = (menuItem) => {
    setActiveMenuItem(menuItem)
    if (menuItem === "setting") {
      setTabs("setting")
    } else if (menuItem === "logout") {
      localStorage.removeItem("token")
      navigate("/login")
    }
  }

  return (
    <div className="w-full min-h-[64px] bg-white py-[13px] px-6 flex gap-1 justify-end items-center relative">
      <div className="w-[38px] h-[38px] rounded-[50%] bg-[#f1f1f1] flex justify-center items-center font-medium text-[#000]">
        {userInitial ? userInitial : "user"}
      </div>
      <p className="text-[#000] text-sm font-medium !m-0">{userName}</p>
      <div className="relative cursor-pointer" onClick={toggleDropdown}>
        <DropdownIcon />
      </div>
      {isDropdownOpen && (
        <div className="absolute top-[70%] w-[144px] p-2 flex flex-col gap-2 bg-white border rounded shadow-md">
          <p className={`text-[#000] p-1 !m-0 text-xs cursor-pointer hover:text-[#2E68BF] ${activeMenuItem === "profile" ? "bg-[#F7FAFF] text-[#2E68BF]" : ""}`}>
            View Profile
          </p>
          <p
            onClick={() => handleMenuItemClick("setting")}
            className={`text-[#000] p-1 !m-0 text-xs cursor-pointer hover:text-[#2E68BF] ${activeMenuItem === "setting" ? "bg-[#F7FAFF] text-[#2E68BF]" : ""}`}
          >
            Setings
          </p>
          <p
            className="text-[#000] p-1 !m-0 text-xs cursor-pointer hover:text-[#2E68BF]"
            onClick={() => {
              handleMenuItemClick("logout")
            }}
          >
            Logout
          </p>
        </div>
      )}
    </div>
  )
}

export default Header
