import React, { useState } from 'react'

import Button from 'react-bootstrap/Button'

import { CrossIcon } from '../IconLibrary'

import PopUp from '../../utils/popup'
import { PlatformModal } from '../LtiRegistration/platformModal'
import { LtiFormModal } from '../licenseeRegistration/licenseeModal'

const ModalCom = (props) => {
  const { tabs, setRegistrationModal, initialData } = props
  const [show, setShow] = useState(true)

  return (
    <>
      <div className="fixed bg-blueRGB top-0 right-0 w-full h-full z-[1060] overflow-y-auto">
        {/*{show && (
          <div className="min-w-[496px] absolute top-[50%] right-[30%]">
            <PopUp Icon />
          </div>
        )} */}
        <Button
          className="absolute top-10 right-[3%] bg-transparent !p-[2px] !border-none"
          onClick={() => setRegistrationModal(false)}
        >
          <CrossIcon w={24} h={24} color="#000" />
        </Button>
        <div className="bg-white !opacity-[1] flex justify-start items-start p-10 w-full h-full max-w-[999px] float-right overflow-auto">
          <div className="h-full min-w-full">
            {tabs === 'lti' ? (
              <PlatformModal setShow={setShow} setRegistrationModal={setRegistrationModal} initialData={initialData} />
            ) : (
              <LtiFormModal setShow={setShow} setRegistrationModal={setRegistrationModal} initialData={initialData} />
            )}
          </div>
        </div>
      </div>
    </>
  )
}

export default ModalCom
