import React, { useState } from "react"

import { Formik } from "formik"

import Button from "../../utils/Button"
import { DropdownIcon, Loading } from "../IconLibrary"
import {
  showErrorReducer,
  showSuccessReducer,
} from "../../Store/reducers/c2eSettings"
import { platformRegister } from "../../Services/registration"
import { useDispatch } from "react-redux"
import C2eAlert from "../../utils/Alert"

export const PlatformModal = ({ setRegistrationModal, initialData }) => {
  const dispatch = useDispatch()

  // const [activityChecked, setActivityChecked] = useState(false)
  // const [playlistChecked, setPlaylistChecked] = useState(false)
  // const [projectChecked, setProjectChecked] = useState(false)

  // const handleActivityToggle = () => {
  //   setActivityChecked(!activityChecked)
  // }

  // const handlePlaylistToggle = () => {
  //   setPlaylistChecked(!playlistChecked)
  // }

  // const handleProjectToggle = () => {
  //   setProjectChecked(!projectChecked)
  // }
  return (
    <Formik
      initialValues={{
        url: initialData?.platformUrl || "",
        name: initialData?.platformName ||"",
        clientId: initialData?.clientId ||  "",
        authenticationEndpoint: initialData?.authEndpoint ||"",
        accesstokenEndpoint: initialData?.accesstokenEndpoint || "",
        authConfigMethod: initialData?.authConfig?.method || "",
        authConfigKey: initialData?.authConfig?.key ||"",
      }}
      validate={(values) => {
        const errors = {}
        if (!values.url) {
          errors.url = "Required"
        }
        if (!values.name) {
          errors.name = "Required"
        }
        if (!values.clientId) {
          errors.clientId = "Required"
        }
        if (!values.authenticationEndpoint) {
          errors.authenticationEndpoint = "Required"
        }
        if (!values.accesstokenEndpoint) {
          errors.accesstokenEndpoint = "Required"
        }
        if (!values.authConfigMethod) {
          errors.authConfigMethod = "Required"
        }
        if (!values.authConfigKey) {
          errors.authConfigKey = "Required"
        }
        return errors
      }}
      onSubmit={async (values, { setSubmitting }) => {
        try {
          dispatch(showErrorReducer());
          const request = await platformRegister(values);
          if (request?.code === 200) {
            dispatch(showSuccessReducer("Registered Successfully!"));
          } else {
            dispatch(showErrorReducer("Something went wrong!"));
          }
        } catch (error) {
          console.error('Error in onSubmit:', error);
          dispatch(showErrorReducer("Something went wrong!"));
        } finally {
          setSubmitting(false);
        }
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <form
          onSubmit={handleSubmit}
          className="flex flex-col justify-between h-full"
        >
          <C2eAlert type="error" />
          <div>
            <div className="w-full flex flex-col items-start">
              <h2 className="text-[#084892] text-left text-[31px] mb-[39px] font-semibold py-[15px] leading-6 border-b-[3px] border-b-[#f8af2c]">
                Add Platform
              </h2>

              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">LMS URL:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="url"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.url}
                    />
                    {errors.url && touched.url && (
                      <div className="error"> {errors.url}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Client Name:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="name"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.name}
                    />
                    {errors.name && touched.name && (
                      <div className="error">{errors.name}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Client Id:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="clientId"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.clientId}
                    />
                    {errors.clientId && touched.clientId && (
                      <div className="error"> {errors.clientId}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Authentication Endpoint:</h4>
                    <div className="input flex justify-between">
                      <input
                        type="text"
                        className="w-full focus:outline-none"
                        placeholder="Input text here"
                        name="authenticationEndpoint"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.authenticationEndpoint}
                      />
                      <DropdownIcon />
                    </div>

                    {errors.authenticationEndpoint && touched.authenticationEndpoint && (
                      <div className="error">{errors.authenticationEndpoint}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Accesstoken Endpoint:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="accesstokenEndpoint"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.accesstokenEndpoint}
                    />
                    {errors.accesstokenEndpoint && touched.accesstokenEndpoint && (
                      <div className="error"> {errors.accesstokenEndpoint}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Auth Config Method:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="authConfigMethod"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.authConfigMethod}
                    />
                    {errors.authConfigMethod && touched.authConfigMethod && (
                      <div className="error">{errors.authConfigMethod}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Auth Config Key:</h4>
                    <input
                      type="text"
                      placeholder="Input text here"
                      className="input"
                      name="authConfigKey"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.authConfigKey}
                    />
                    {errors.authConfigKey && touched.authConfigKey && (
                      <div className="error"> {errors.authConfigKey}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
            </div>
            {/*<h4 className="heading4 mb-4">Visibility</h4>}
            <div className="flex items-center text-sm gap-1">
              <h5 className="heading5">Activity</h5>
              <label className="switch">
                <input
                  className="checkbox"
                  type="checkbox"
                  checked={activityChecked}
                  onChange={handleActivityToggle}
                />
                <span className="slider"></span>
              </label>

              <h5 className="heading5">Playlist</h5>
              <label className="switch">
                <input
                  className="checkbox"
                  type="checkbox"
                  checked={playlistChecked}
                  onChange={handlePlaylistToggle}
                />
                <span className="slider"></span>
              </label>

              <h5 className="heading5">Project</h5>
              <label className="switch">
                <input
                  className="checkbox"
                  type="checkbox"
                  checked={projectChecked}
                  onChange={handleProjectToggle}
                />
                <span className="slider"></span>
              </label>
                    </div>*/}
          </div>
          <div className="flex items-center gap-[9px] justify-end">
            <Button
              type="button"
              className=" text-primary py-[4px] rounded-[5.16px] border border-primary px-[30px] shadow-btnSmShadow"
              text={" Cancel"}
              cta={() => {
                setRegistrationModal(false)
              }}
            />

            <Button
              type="submit"
              bg="bg-[#084892]"
              className="text-white py-[4px] rounded-[5.16px] border border-primary px-[43px] shadow-btnSmShadow"
              text={"Save"}
              Icon={isSubmitting ? <Loading /> : ""}
              disabled={isSubmitting}
              cta={() => {
              }}
            />
          </div>
        </form>
      )}
    </Formik>
  )
}
