import React, { useState } from "react"

import { Formik } from "formik"

import Button from "../../utils/Button"
import { BlueDropdown, DropdownIcon, Loading } from "../IconLibrary"
import {
  showErrorReducer,
  showSuccessReducer,
} from "../../Store/reducers/c2eSettings"
import { useDispatch } from "react-redux"
import { addLicensee } from "../../Services/registration"
import C2eAlert from "../../utils/Alert"

export const LtiFormModal = ({ setRegistrationModal, initialData }) => {
  const dispatch = useDispatch()

  return (
    <Formik
      initialValues={{
        lti_client_id: initialData.lti_client_id || "",
        cee_licensee_id: initialData?.cee_licensee_id || "",
        cee_secret_key: initialData?.cee_secret_key || "",
        cee_provider_url: initialData?.cee_provider_url || "",
      }}
      validate={(values) => {
        const errors = {}
        if (!values.lti_client_id) {
          errors.lti_client_id = "Required"
        }
        if (!values.cee_licensee_id) {
          errors.cee_licensee_id = "Required"
        }
        if (!values.cee_secret_key) {
          errors.cee_secret_key = "Required"
        }
        if (!values.cee_provider_url) {
          errors.cee_provider_url = "Required"
        }
        return errors
      }}
      onSubmit={async (values, { setSubmitting }) => {
        try {
          dispatch(showErrorReducer())
          const request = await addLicensee(values)
          if (request?.code === 200) {
            dispatch(showSuccessReducer("Registered Successfully!"))
          } else {
            dispatch(showErrorReducer("Something went wrong!"))
          }
        } catch (error) {
          console.error("Error in onSubmit:", error)
          dispatch(showErrorReducer("Something went wrong!"))
        } finally {
          setSubmitting(false)
        }
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <form
          onSubmit={handleSubmit}
          className="flex flex-col justify-between h-full"
        >
          <C2eAlert type="error" />
          <div>
            <div className="w-full flex flex-col items-start">
              <h2 className="text-[#084892] text-left text-[31px] mb-[39px] font-semibold py-[15px] leading-6 border-b-[3px] border-b-[#f8af2c]">
                Add Licensee
              </h2>
              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">ClientId:</h4>
                    <div className="input flex justify-between">
                      <input
                        type="text"
                        className="w-full focus:outline-none"
                        placeholder="Enter Tool Name"
                        name="lti_client_id"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.lti_client_id}
                      />
                      <BlueDropdown />
                    </div>

                    {errors.lti_client_id && touched.lti_client_id && (
                      <div className="error">{errors.lti_client_id}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">Licensee:</h4>
                    <input
                      type="text"
                      placeholder="Enter Tool URL"
                      className="input"
                      name="cee_licensee_id"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cee_licensee_id}
                    />
                    {errors.cee_licensee_id && touched.cee_licensee_id && (
                      <div className="error"> {errors.cee_licensee_id}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
              <div className="w-full flex gap-6 items-start">
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">C2E Secret:</h4>
                    <input
                      type="text"
                      placeholder="Enter Tool Name"
                      className="input"
                      name="cee_secret_key"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cee_secret_key}
                    />
                    {errors.cee_secret_key && touched.cee_secret_key && (
                      <div className="error">{errors.cee_secret_key}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
                <div className="w-full">
                  <div className="mb-4">
                    <h4 className="heading4">C2E Provider URL:</h4>
                    <div className="input flex justify-between">
                      <input
                        type="text"
                        className="w-full focus:outline-none"
                        placeholder="Enter Tool URL"
                        name="cee_provider_url"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.cee_provider_url}
                      />
                      <DropdownIcon />
                    </div>

                    {errors.cee_provider_url && touched.cee_provider_url && (
                      <div className="error"> {errors.cee_provider_url}</div>
                    )}
                    {/* Required */}
                  </div>
                </div>
              </div>
              <div className="w-full flex gap-6 items-start"></div>
            </div>
          </div>
          <div className="flex items-center gap-[9px] justify-end">
            <Button
              type="button"
              className=" text-primary py-[4px] rounded-[5.16px] border border-primary px-[30px] shadow-btnSmShadow"
              text={" Cancel"}
              cta={() => {
                setRegistrationModal(false)
              }}
            />

            <Button
              type="submit"
              bg="bg-[#084892]"
              className="text-white py-[4px] rounded-[5.16px] border border-primary px-[43px] shadow-btnSmShadow"
              text={"Save"}
              Icon={isSubmitting ? <Loading /> : ""}
              disabled={isSubmitting}
              cta={() => {
                // handleLogin('popup');
              }}
            />
          </div>
        </form>
      )}
    </Formik>
  )
}
