import DataTable from "react-data-table-component"
import Elipsis from "../../assets/images/elipsis.svg"
import { DeleteIcon, EyeIcon } from "../IconLibrary"
import { useState } from "react"
import { Dropdown } from "react-bootstrap"
import { deleteLicensee } from "../../Services/registration"
import PopUp from "../../utils/popup"

const customStyles = {
  headRow: {
    style: {
      backgroundColor: "#F6F6F6",
      padding: "16px",
    },
  },
  headCells: {
    style: {
      color: "#767676",
      fontSize: "14px",
      fontWeight: "medium !important",
    },
  },
  rows: {
    style: {
      color: "#767676",
    },
  },
}

const DataTableComp = ({
  licenseeData,
  searchQuery,
  setRegistrationModal,
  setInitialData,
}) => {
  const filteredData = searchQuery
    ? licenseeData?.result?.filter((data) =>
        data.platformName?.toLowerCase()?.includes(searchQuery?.toLowerCase())
      )
    : licenseeData
  const columns = [
    {
      name: "LMS Client Id",
      selector: (row) => row.lti_client_id,
    },
    {
      name: "Client Name",
      selector: (row) => row.platformName,
    },
    {
      name: "Licensee",
      selector: (row) => row.cee_licensee_id,
    },
    {
      name: "Create Date",
      selector: (row) => row.createdAt,
    },
    {
      name: "Update Date",
      selector: (row) => row.updatedAt,
    },
    {
      cell: (row) => (
        <ActionsDropdown
          data={row}
          setRegistrationModal={setRegistrationModal}
          setInitialData={setInitialData}
        />
      ),
    },
  ]
  return (
    <>
      {searchQuery ? (
        <DataTable
          columns={columns}
          data={filteredData}
          customStyles={customStyles}
        />
      ) : (
        <DataTable
          columns={columns}
          data={filteredData?.result}
          customStyles={customStyles}
        />
      )}
    </>
  )
}

export default DataTableComp

const ActionsDropdown = ({ data, setRegistrationModal, setInitialData }) => {
  const [showDropdown, setShowDropdown] = useState(false)
  const [showDeletePopup, setShowDeletePopup] = useState(false)

  const handleDropdownToggle = () => {
    setShowDropdown(!showDropdown)
  }

  const handleViewClick = (data) => {
    setInitialData(data)
    setRegistrationModal(true)
    setShowDropdown(false)
  }

  const handleDeleteClick = async () => {
    setShowDeletePopup(true)
    setShowDropdown(false)
  }
  const handleCancelDelete = () => {
    setShowDeletePopup(false)
  }
  const handleConfirmDelete = async () => {
    try {
      const response = await deleteLicensee(data.id)
      console.log("Response from deleteLicensee API:", response)
    } catch (error) {
      console.error("Error in handleConfirmDelete:", error)
    } finally {
      setShowDeletePopup(false)
    }
  }
  return (
    <>
      <Dropdown show={showDropdown} onToggle={handleDropdownToggle}>
        <Dropdown.Toggle variant="link" id={"table-dropdown-toggle"}>
          <img src={Elipsis} alt="elipsis" />
        </Dropdown.Toggle>

        <Dropdown.Menu>
          <Dropdown.Item onClick={() => handleViewClick(data)}>
            <div className="flex flex-row gap-1 items-center">
              <EyeIcon />
              View
            </div>
          </Dropdown.Item>
          <Dropdown.Item onClick={handleDeleteClick}>
            <div className="flex flex-row gap-1 items-center">
              <DeleteIcon />
              Delete
            </div>
          </Dropdown.Item>
        </Dropdown.Menu>
      </Dropdown>
      {showDeletePopup && (
        <PopUp
          text="Are you sure?"
          subtitle="You won't be able to revert this!"
          handleCancelDelete={handleCancelDelete}
          handleConfirmDelete={handleConfirmDelete}
        />
      )}
    </>
  )
}
