import React, { useState } from "react"
import Dropdown from "react-bootstrap/Dropdown"

const RowSelect = (props) => {
  const { text, options } = props
  const [selectedValue, setSelectedValue] = useState(options[0]?.name || "")

  const handleSelect = (value) => {
    setSelectedValue(value)
  }

  return (
    <div className="flex flex-col">
      <span className="spanText">{text ? text : "Rows per page"}</span>
      <Dropdown onSelect={handleSelect}>
        <Dropdown.Toggle className="bg-white !text-[#084892] w-[129px] !flex justify-between items-center">
          {selectedValue}
        </Dropdown.Toggle>

        <Dropdown.Menu>
          {options.map((value) => {
            return (
              <Dropdown.Item
                className="!text-[#084892]"
                key={value.id}
                eventKey={value.name}
              >
                {value.name}
              </Dropdown.Item>
            )
          })}
        </Dropdown.Menu>
      </Dropdown>
    </div>
  )
}

export default RowSelect
