import React from "react"
import { CrossIcon } from "../../components/IconLibrary"
import information from "../../assets/images/informative.svg"
import Button from "../Button"

const PopUp = (props) => {
  const {
    Icon,
    text,
    color,
    subtitle,
    handleConfirmDelete,
    handleCancelDelete,
  } = props

  return (
    <div className="popup-container">
      <div className="popup-content">
        <div className="popup-header">
          <div className="close-icon" onClick={handleCancelDelete}>
            <CrossIcon color="#000" />
            {/* <img src={Icon} alt='logo'/> */}
          </div>
        </div>
        <div className="popup-body">
          {<img src={information} alt="logo" />}
          <div className="flex flex-col justify-center items-center">
            {text ? (
              text
            ) : (
              <p
                className={`max-w-[384px] w-full text-2xl leading-7 font-light m-0 ${
                  color ? color : "text-[#084892]"
                }`}
              >
                {text}
                LMS settings added successfully
              </p>
            )}

            <p className={`${color ? color : "text-[#515151]"}`}>
              {subtitle ? subtitle : "Please try again later"}
            </p>
          </div>

          <div className="flex flex-row gap-6 ">
            <Button
              type="button"
              className={`border border-primary shadow-btnShadow ${"text-[#084892] "}`}
              text="Cancel"
              cta={handleCancelDelete}
            />
            <Button
              type="submit"
              className={`border bg-[#084892] shadow-btnShadow ${"text-[#FFFFFF] "}`}
              text="Delete it"
              cta={handleConfirmDelete}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default PopUp
