import React, { useEffect, useState } from "react"

import Button from "react-bootstrap/Button"

import Lms from "../../assets/images/lms.svg"
import { AddIcon, Search } from "../IconLibrary"
import RowSelect from "../../utils/Select/rowSelect"
import Pagination from "../../utils/Pagination"
import LtiDataTable from "./ltiDataTable"
import { getPlatformData } from "../../Services/registration"

const LtiRegistraton = ({ setInitialData, setRegistrationModal }) => {
  const [searchQuery, setSearchQuery] = useState("")
  const [platformData, setPlatformData] = useState()

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value)
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await getPlatformData()
        setPlatformData(res)
      } catch (error) {
        console.log(error)
      }
    }
    fetchData()
  }, [])

  return (
    <div className="w-full">
      <div className="flex flex-col justify-center gap-2 items-start">
        <img className="w-[48px] h-[100%]" src={Lms} alt="logo" />
        <div className="flex flex-col gap-1">
          <h2 className="heading2">LTI Registration</h2>
          <p className="text-[17px] leading-5 font-normal text-[#767676]">
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry.
          </p>
        </div>
        <div className="w-full flex justify-between items-center rounded bg-[#F7FAFF] p-4">
          <div className="flex items-center gap-4 ">
            <div className="relative">
              <div className="w-[230px] focus:outline-0 border-b-[2px] border-b-[#084892] ">
                <input
                  type="search"
                  className="w-[200px] h-9 p-1 bg-transparent text-[#084892] placeholder-[#084892] focus:outline-0 "
                  placeholder="Search"
                  value={searchQuery}
                  onChange={handleSearchChange}
                />
              </div>
              <div className="absolute top-1 right-2 bottom-0">
                <Search />
              </div>
            </div>
            <div className="flex flex-col gap-1">
              <RowSelect
                options={[
                  { name: 10, id: 10 },
                  { name: 20, id: 20 },
                  { name: 30, id: 30 },
                ]}
              />
            </div>
            {/* <div className="flex flex-col gap-1">
              <span className="spanText">Filter By Type</span>
            </div> */}
          </div>
          <Button
            className="!flex items-center gap-2 !rounded-2xl !bg-[#084892] shadow-btnShadow"
            onClick={() => setRegistrationModal(true)}
            type="button"
          >
            <AddIcon />
            Add Platform
          </Button>
        </div>
      </div>
      <div>
        <Pagination />
      </div>
      <LtiDataTable
        setInitialData={setInitialData}
        searchQuery={searchQuery}
        platformData={platformData}
        setRegistrationModal={setRegistrationModal}
      />
      <Pagination />
    </div>
  )
}

export default LtiRegistraton
