import React, { useEffect, useState } from "react"
import ModalCom from "../components/Modal"
import Layout from "../components/Layout"
import Footer from "../components/Layout/footer"
import Header from "../components/Layout/header"
import LtiRegistraton from "../components/LtiRegistration"
import LicenseeRegistraton from "../components/licenseeRegistration"
import Setting from "../components/Settings"
import { getProfileData } from "../Services/auth"

const Dashboard = (props) => {
  const { tabs, setTabs, RegistrationModal, setRegistrationModal } = props
  const [userName, setUserName] = useState("")
  const [userInitial, setUserInitial] = useState("")
  const [initialData, setInitialData] = useState({})

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const res = await getProfileData()
        const email = res?.data?.result?.email
        setUserName(email)
        setUserInitial(email ? email.charAt(0).toUpperCase() : "")
      } catch (error) {
        console.error("Error fetching profile:", error)
      }
    }

    fetchProfile()
  }, [])
  return (
    <div className="flex">
      <Layout tabs={tabs} setTabs={setTabs} />

      <div className="w-full bg-[#f1f1f1]">
        <Header
          setTabs={setTabs}
          userInitial={userInitial}
          userName={userName}
        />
        {RegistrationModal && (
          <ModalCom
            tabs={tabs}
            RegistrationModal={RegistrationModal}
            setRegistrationModal={setRegistrationModal}
            initialData={initialData}
          />
        )}

        <div className="w-full flex justify-start min-h-[900px] items-start">
          <div className="w-full min-h-[850px] p-[24px] bg-white !m-6">
            {tabs === "lti" ? (
              <LtiRegistraton
                setRegistrationModal={setRegistrationModal}
                setInitialData={setInitialData}
              />
            ) : tabs === "setting" ? (
              <Setting userInitial={userInitial} />
            ) : (
              <LicenseeRegistraton
                setRegistrationModal={setRegistrationModal}
                setInitialData={setInitialData}
              />
            )}
          </div>
        </div>
        <Footer />
      </div>
    </div>
  )
}

export default Dashboard
