import React, { useState, useEffect } from 'react'

import { Routes, Route } from 'react-router-dom'

import { useDispatch } from 'react-redux'
import { getProfileDataRequest } from '../Actions/auth'
import OpenRoute from './openRouter'
import Login from '../pages/login'
import SignUp from '../pages/sign-up'
import ForgotPassword from '../pages/forget-password'
import NewConfirmPassword from '../pages/reset-new-password'

import Dashboard from '../pages/dashboard'
import PrivateRoute from './privateRoutes'

const App = () => {
  const dispatch = useDispatch()

  const [tabs, setTabs] = useState('lti')
  const [RegistrationModal, setRegistrationModal] = useState("")
  const [showPassword, setshowPassword] = useState(false)

  // useEffect(() => {
  //   dispatch(getProfileDataRequest())
  // }, [dispatch])

  return (
    <>
      <Routes>
        <Route
          path="/login"
          element={
            <OpenRoute>
            <Login
              showPassword={showPassword}
              setshowPassword={setshowPassword}
            />
            </OpenRoute>
          }
        />
        <Route
          path="/sign-up"
          element={
            <SignUp
              showPassword={showPassword}
              setshowPassword={setshowPassword}
            />
          }
        />
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route
          path="/reset-password"
          element={
            <NewConfirmPassword
              showPassword={showPassword}
              setshowPassword={setshowPassword}
            />
          }
        />
        <Route
          path="/"
          element={
            <PrivateRoute count="1">
            <Dashboard
              tabs={tabs}
              setTabs={setTabs}
              RegistrationModal={RegistrationModal}
              setRegistrationModal={setRegistrationModal}
            />
            </PrivateRoute>
          }
        />
      </Routes>
    </>
  )
}

export default App
